  align-items: center;
  justify-content: center;
  font-family: "OutfitThin";
  font-size: 18pt;
}
.erazer_cup .info_box_prod.white {
  color: white;
}
.erazer_cup .info_box_prod.gold {
  color: #ffdd33;
}
.erazer_cup .header_container_prod {
  display: flex;
  gap: 10px;
  box-shadow: none;
  border-radius: 0;
}
.erazer_cup .header_container_prod .rank_header_prod {
  width: 90px;
  height: 40px;
  box-shadow: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 36px;
  color: white;
  font-family: "OutfitThin";
  background-color: transparent;
}
.erazer_cup .header_container_prod .name_header_prod {
  width: 710px;
  height: 40px;
  box-shadow: none;
  border-radius: 0;
  display: flex;
  justify-content: center;
